import { BrowserRouter, Route, Routes } from "react-router-dom";
import MainLayout from "./components/Layout";
import Dashboard from "./pages/Dashboard";
import AdsMonitor from "./pages/AdsMonitor";

const App = () => {
    return (
        <BrowserRouter>
            <Routes>
                <Route 
                    index 
                    element={
                        <MainLayout>
                            <Dashboard/>
                        </MainLayout>
                    }
                />
                <Route 
                    path='/adsMonitor' 
                    element={
                        <MainLayout>
                            <AdsMonitor/>
                        </MainLayout>
                    }
                />
            </Routes>
        </BrowserRouter>
    )
};

export default App;